import {UserApi} from '@/utils/api/user'
export default {
  middleware: ["auth"],
  data() {
    return {
      user: null
    }
  },
  async asyncData() {
    const {data} = await UserApi.getUser();
    return {
      user: data.user
    }
  },
  methods: {
    logout() {
    // 移除 用户信息
        this.$router.replace('/');
        this.$store.commit('user/setUser', '');
    },
    async updateUser() {
     const {data} = await  UserApi.updateUser(this.user);
     if(data){
       this.user = data.user;
     }
    }
  }
};
